@import '../../SassVariables/mixins';
.header-cont {
  position: relative;
  height: auto;
  margin-top: 20px;
  @include adjust-to(handhelds) {
    height: 500px;
    margin: 30px auto;
    text-align: center;
  }
  &__title {
    position: absolute;
    left: 0;
    transition: left 0.3s;
    width: 350px;
    font-family: 'Oswald', sans-serif;
    color: white;
    opacity: 0.7;
    @include adjust-to(handhelds) {
      top: 60px;
      left: 140px;
      border: 5px solid white;
      text-align: left;
    }
    @include adjust-to(custom){
      left: 650px
    }
    &__h1 {
      margin-top: 5px;
      margin-bottom: 30px;
      width: 372px;
      height: 182px;
      font-family: 'Oswald', sans-serif;
      font-size: 58px;
      font-weight: 500;
      line-height: 61.38px;
    }
  }
  &__picture {
    max-height: 100%;
    height: auto;
    max-width: 100%;
    width: auto;
    border-radius: 10px;
    -webkit-box-shadow: 14px -7px 19px 3px rgba(0, 0, 0, 0.51);
    -moz-box-shadow: 14px -7px 19px 3px rgba(0, 0, 0, 0.51);
    box-shadow: 14px -7px 19px 3px rgba(0, 0, 0, 0.51);
  }
}